.AlertBlock {
  display: block;
  max-width: 500px;
  min-width: 400px;
  background: #fff;
  box-shadow: 2px 2px 4px 0px #000;
  border-radius: 1px;
  position: relative;
}

.AlertBlock + .AlertBlock {
  margin: 14px 0 0;
}

.AlertBlock__body {
  padding: 15px;
}

.AlertBlock__text {
  font-family: 'Courier New', Courier, monospace;
  font-size: 20px;
  line-height: 27px;
}

.AlertBlock__text--info {
  color: #000;
}

.AlertBlock__text--error {
  color: red;
}

.AlertBlock__close {
  cursor: pointer;
  width: 24px;
  height: 24px;
  font-size: 24px;
  line-height: 24px;
  color: #000;
  position: absolute;
  top: 1px;
  right: 1px;
  user-select: none;
}

.AlertBlock__close:hover {
  background: #000;
  color: #fff;
}
