.Rect {
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    height: 0;
    background: transparent;
    cursor: move;
    overflow: hidden;
    box-sizing: border-box;
    touch-action: none;
    pointer-events: all;

    border: 1px solid #000;
}

.Rect>div {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    border: 2px dashed #fff;
}

.Rect>div>div {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    border: 1px solid #000;
}
