.InputColorItem {
  width: 20px;
  height: 20px;
  border: 1px solid #000;
  cursor: pointer;
  user-select: none;
  box-shadow: 1px 1px 0px 0px #000;
}

.InputColorItem--selected {
  cursor: default;
  box-shadow: none;
}

.InputColorItem:hover {
}

.InputColorItem--selected:hover {
  box-shadow: none;
}
