.AlertPanelFaBell {
  cursor: pointer;
  line-height: 16px;
}

.AlertPanelFaBell__icon {
  width: 16px;
  height: 16px;
  font-size: 16px;
  line-height: 16px;
  color: #000;
  display: inline-block;
  vertical-align: middle;
}

.AlertPanelFaBell__count {
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
  line-height: 14px;
}

.AlertPanelFaBell:hover .AlertPanelFaBell__icon {
  color: blue;
}

.AlertPanelFaBell:hover .AlertPanelFaBell__count {
  color: blue;
}

.AlertPanel {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  overflow: hidden;
  transition: width 0.2s ease-in 0.1s;
}

.AlertPanel--open {
  width: 400px;
  box-shadow: -2px 0px 4px 0px #000;
}

.AlertPanel--close {
  transition: none;
  width: 0px;
}

.AlertPanel__body {
  position: relative;
  width: 400px;
  height: 100%;
}

.AlertPanel__scroll {
  height: 100%;
  overflow: auto;
}

.AlertPanel__padding {
  padding: 27px;
}

.AlertPanel__removeAll {
  padding: 20px 0 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.AlertPanel__close {
  cursor: pointer;
  width: 24px;
  height: 24px;
  font-size: 24px;
  line-height: 24px;
  color: #000;
  position: absolute;
  left: 1px;
  top: 1px;
  user-select: none;
}

.AlertPanel__close:hover {
  background: #000;
  color: #fff;
}
