.FigureText {
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
    background: #000;
    box-sizing: border-box;
}

.FigureText__showHover {
    cursor: pointer;
}

.FigureText__showHover:hover {
    outline: 3px dashed #000;
    outline-offset: 2px;
}

.FigureText__body {
    padding: 10px;
}

.FigureText__text {
    user-select: none;
    font-family: 'Courier New', Courier, monospace;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.6;
    font-weight: normal;
    white-space: pre-wrap;
    text-align: left;
}