.Button {
    display: inline-block;
    vertical-align: middle;

    cursor: pointer;
    box-sizing: border-box;
    padding: 4px 9px;
    margin: 0;
    background: #1f7fad;
    color: #fff;
    border: none;
    outline: none;
    user-select: none;
    box-shadow: 1px 1px 0 0 #000;
}

.Button--size1 {
    padding: 4px 9px;
}

.Button--size2 {
    padding: 4px 9px;
}

.Button--size3 {
    padding: 4px 9px;
}


.Button--full {
    display: block;
    width: 100%;
}

.Button:hover {
    background: #000b9c;
}

.Button:active {
    box-shadow: none;
}

.Button:focus {
    outline: 1px dashed #000;
    outline-offset: 2px;
}