.Loading {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  opacity: 0.8;
  background: #f1f2f2;
  user-select: none;
  pointer-events: none;
  overflow: hidden;
}

.Loading__spinner {
  display: block;
  animation-name: spin;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  margin: auto;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Loading__spinner--big {
  height: 125px;
}

.Loading__spinner--medium {
  height: 75px;
}

.Loading__spinner--small {
  height: 50%;
}

.Loading__spinner--very-small {
  height: 100%;
}
