.TDisplay {
  background: green;
  box-shadow: 2px 2px 4px 0px #000;
  border-radius: 1px;
}

.TDisplay--isDragging {
  box-shadow: 3px 3px 4px 0px #000;
  position: relative;
  top: -1px;
  left: -1px;
}

.TDisplay__head {
  background: blue;
  min-height: 24px;
  user-select: none;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.TDisplay__head--draggable {
  cursor: move;
}

.TDisplay__close {
  inset-block-start: 0;
  inset-inline-end: 0;
  cursor: pointer;
  height: 24px;
  font-size: 24px;
  line-height: 24px;
  color: #000;
}

.TDisplay__close:hover {
  color: #ff0000;
}

.TDisplay__body {
  background: #fff;
}
