.InputTextArea {
  display: block;
  margin: 0;
  padding: 0;
  padding: 10px;
  font-family: "Courier New", Courier, monospace;
  font-size: 14px;
  outline: none;
  border: 2px solid #000;
  border-radius: 2px;
  box-sizing: border-box;
  width: 100%;
  color: #000;
  background: #fff;
  min-height: 80px;
}

.InputTextArea:active {
  border-color: #000b9c;
}

.InputTextArea:focus {
  border-color: #000b9c;
}
