.BeepItem {
  position: relative;
  background: #fff;
  box-shadow: 1px 1px 3px 0px #000;
  border-radius: 1px;
}

.BeepItem__body {
  padding: 20px;
}

.BeepItem__text {
  font-size: 14px;
  line-height: 14px;
}

.BeepItem__text--info {
  color: #000;
}

.BeepItem__text--error {
  color: red;
}

.BeepItem__remove {
  position: absolute;
  top: 1px;
  right: 1px;
  cursor: pointer;
  width: 24px;
  height: 24px;
  font-size: 24px;
  line-height: 24px;
  color: #000;
  user-select: none;
}

.BeepItem__remove:hover {
  background: #000;
  color: #fff;
}
