.Layout {
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: stretch;
    justify-content: flex-start;
    position: relative;
}

.Layout--normal {}

.Layout--light {
    background: url("data:image/png;base64,");
}

.Layout--dark {
    background: url("data:image/png;base64,");
}


.Layout__top {
    flex: 0;
    /* background: aquamarine; */
    position: relative;
}

.Layout__body {
    flex: 1;
    /* background: burlywood; */
    position: relative;
    overflow: auto;
}

.Layout__bottom {
    flex: 0;
    /* background: chartreuse; */
    position: relative;
}