.Circle {
    position: absolute;
    left: 0;
    top: 0;
    background: #fff;
    border: 1px solid #000;
    cursor: pointer;
    width: 14px;
    height: 14px;
    border-radius: 100%;
    overflow: hidden;
    margin: -7px 0 0 -7px;
    box-sizing: border-box;
    touch-action: none;
    pointer-events: all;
}