.dialog {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #0006;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    z-index: 999999;
}

.dialog__body {
    margin: auto;
    box-shadow: 2px 2px 4px 0px #000;
    border-radius: 1px;
    background: #fff;
}